@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Trebuc";
  font-display: swap;
  src: url("../fonts/trebuc.woff2") format("woff2"), url("../fonts/trebuc.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Exo2-reg";
  font-display: swap;
  src: url("../fonts/exo2-regular.woff2") format("woff2"), url("../fonts/exo2-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 100;
  font-family: "Exo2-thin";
  font-display: swap;
  src: url("../fonts/exo2-thin.woff2") format("woff2"), url("../fonts/exo2-thin.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: "Trebuc", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 80px;
}
@media (max-width: 1279px) {
  .container {
    padding: 0 120px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 16px;
  }
}

.product-list {
  position: relative;
  height: 100vh;
  min-height: auto;
  background-color: #000000;
  background-image: url("../../img/content/main-bg.png");
}
@media (max-width: 1279px) {
  .product-list {
    height: auto;
  }
}
.product-list .container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.product-list .title {
  padding-top: 36px;
  margin-bottom: 24px;
  font-size: 36px;
  line-height: 44px;
  color: #ffffff;
  font-family: "Exo2-thin", "Arial", sans-serif;
  text-align: center;
}
.product-list__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 1279px) {
  .product-list__list {
    justify-content: center;
  }
}
@media (max-width: 1023px) {
  .product-list__list {
    flex-direction: column;
    margin: 0 auto;
  }
}
.product-list__item {
  position: relative;
  overflow: hidden;
  margin-bottom: 30px;
}
.product-list__item::before {
  content: "";
  position: absolute;
  z-index: 5;
  top: -35px;
  left: -35px;
  width: 70px;
  height: 70px;
  rotate: 45deg;
  background-image: url("../../img/content/main-bg.png");
}
@media (min-resolution: 1.5dppx) {
  .product-list__item::before {
    background-image: url("../../img/content/main-bg@2x.png");
  }
}
.product-list__item .disabled::before {
  border-right: 4px solid #b3b3b3;
}
@media (max-width: 1279px) {
  .product-list__item {
    margin-right: 80px;
  }
  .product-list__item:last-child, .product-list__item:nth-child(even) {
    margin-right: 0;
  }
}
@media (max-width: 1023px) {
  .product-list__item {
    margin-right: 0;
  }
}

.product-card__link {
  display: flex;
  overflow: hidden;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
  width: 320px;
  height: 480px;
  background-color: #f2f2f2;
  border: 4px solid #1698d9;
  border-radius: 12px;
  margin-bottom: 14px;
  transition: border-color 0.3s ease;
}
@media (max-width: 374px) {
  .product-card__link {
    width: 290px;
  }
}
.product-card__link::before {
  content: "";
  position: absolute;
  top: 19px;
  left: -15px;
  width: 75px;
  height: 4px;
  background-color: #1698d9;
  rotate: -45deg;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .product-card__link:hover {
    border: 4px solid #2ea8e6;
  }
  .product-card__link:hover::before {
    background-color: #2ea8e6;
  }
  .product-card__link:hover .product-card__head {
    color: #2ea8e6;
  }
  .product-card__link:hover .product-card__additional {
    background-color: #2ea8e6;
  }
}
.product-card__info {
  padding: 20px 50px 10px;
}
@media (max-width: 374px) {
  .product-card__info {
    padding: 20px 30px 10px;
  }
}
.product-card__img {
  position: relative;
}
.product-card__head {
  margin-bottom: 5px;
  color: #666666;
  transition: color 0.3s ease;
}
.product-card__selected {
  display: none;
}
.product-card__title {
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
}
.product-card__desc {
  display: block;
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
}
.product-card__path {
  color: #666666;
  font-size: 14px;
  line-height: 16px;
}
.product-card__amt {
  display: inline-block;
  padding-right: 5px;
  font-weight: 700;
}
.product-card__text {
  font-weight: 400;
}
.product-card__additional {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 16px;
  bottom: 0;
  right: 0;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #2ea8e6;
  color: #ffffff;
  text-align: center;
  transition: background-color 0.3s ease;
}
.product-card__weight {
  font-size: 42px;
  line-height: 42px;
}
.product-card__unit {
  font-size: 21px;
  line-height: 22px;
}
.product-card__offer {
  color: #ffffff;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
}
.product-card__offer--selected {
  display: none;
}
.product-card__offer-link {
  color: #2ea8e6;
  font-weight: 700;
  text-decoration: underline dashed;
  transition: opacity 0.3s ease;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .product-card__offer-link:hover, .product-card__offer-link:focus {
    opacity: 0.7;
  }
}

.product-card.selected .product-card__link {
  border: 4px solid #e52e7a;
}
.product-card.selected .product-card__link::before {
  background-color: #e52e7a;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .product-card.selected .product-card__link:hover .product-card__head {
    color: #e52e7a;
  }
  .product-card.selected .product-card__link:hover .product-card__additional {
    background-color: #e52e7a;
  }
  .product-card.selected .product-card__link:hover .product-card__head {
    display: none;
  }
  .product-card.selected .product-card__link:hover .product-card__selected {
    display: block;
    color: #e52e7a;
  }
}
.product-card.selected .product-card__additional {
  background-color: #e52e7a;
}
.product-card.selected .product-card__offer {
  display: none;
}
.product-card.selected .product-card__offer--selected {
  display: block;
}

.product-card.disabled .product-card__link {
  border: 4px solid #b3b3b3;
  cursor: auto;
}
.product-card.disabled .product-card__link::before {
  background-color: #b3b3b3;
}
.product-card.disabled .product-card__link .product-card__info,
.product-card.disabled .product-card__link .product-card__head,
.product-card.disabled .product-card__link .product-card__path {
  color: #b3b3b3;
}
.product-card.disabled .product-card__link .product-card__additional {
  background-color: #b3b3b3;
}
.product-card.disabled .product-card__link .product-card__img {
  opacity: 0.5;
}

.product-card.disabled .product-list__item-offer {
  color: #ffff66;
}

